<template>
  <AppRow only-owner>
    <v-col>
      <v-card
        flat
        class="pa-4"
      >
        <v-row>
          <v-col
            cols="12"
            sm="6"
            md="3"
          >
            <v-text-field
              v-model="message"
              solo
              flat
              hide-details="auto"
              background-color="#464646"
              label="Сообщение"
              class="rounded-lg"
            />
          </v-col>
          <v-col
            cols="12"
            sm="6"
            md="3"
          >
            <v-text-field
              v-model="from"
              solo
              flat
              hide-details="auto"
              background-color="#464646"
              label="От"
              class="rounded-lg"
            />
          </v-col>
          <v-col
            cols="12"
            sm="6"
            md="3"
          >
            <v-autocomplete
              v-model="receivers"
              :items="[]"
              solo
              flat
              hide-details="auto"
              clearable
              background-color="#464646"
              label="Получатели (по умолч. все)"
              item-text="username"
              item-value="id"
              class="rounded-lg"
              multiple
            >
              <template v-slot:selection="{ attrs, item, select, selected }">
                <v-chip
                  v-bind="attrs"
                  :input-value="selected"
                  close
                  @click="select"
                  @click:close="remove(item)"
                >
                  <v-avatar left>
                    <v-img :src="item.img" />
                  </v-avatar>
                  {{ item.username }}
                </v-chip>
              </template>

              <template v-slot:item="{ item }">
                <v-list-item-avatar>
                  <img :src="item.img">
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-title>{{ item.username }}</v-list-item-title>
                  <v-list-item-subtitle>ID: {{ item.id }}</v-list-item-subtitle>
                </v-list-item-content>
              </template>
            </v-autocomplete>
          </v-col>
          <v-col
            cols="12"
            sm="6"
            md="3"
          >
            <v-btn
              block
              depressed
              color="primary"
              class="text-none rounded-lg"
              height="48"
              x-large
            >
              Отправить
            </v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-col>
  </AppRow>
</template>

<script>
import AppRow from '@/components/AppRow.vue';

export default {
  components: {
    AppRow,
  },
  data() {
    return {
      message: '',
      from: '',
      receivers: [],
    };
  },
  methods: {
    remove(item) {
      const index = this.receivers.indexOf(item.id);
      if (index >= 0) this.receivers.splice(index, 1);
    },
    onNotification() {
      this.message = '';
      this.from = '';
      this.receivers = [];
    },
  },
};
</script>
